	module.exports = {"data":{"talkJson":{"description":"Javascript has always been a language with very little syntactic sugar—for better or worse. With ES6/2015, and future iterations,though, Javascript is gaining a more and more abstract and expressive syntax. To some it might appear that our language—which already seems accessible and approachable for beginners— is becoming even more accessible and approachable. However, both the humanities and CS education research have proven that abstraction, while a powerful tool for knowledgeable practitioners, can be an equally powerful foil for beginners. As we enter the era of language-level abstractions in ES6/2015, we are charged with the task of rethinking how we teach JavaScript. Through an interdisciplinary montage I will identify the problem of teaching abstraction as a ubiquitous demand across nearly every domain, and align the issues of creativity and critical thinking in the humanities with issues in computer science. The talk will conclude with a discussion of how the discipline of computer science and that of the humanities can inform each other to produce more effective and creative solutions to both developing and teaching abstractions.","meta":{"duration":"PT25M49S"},"presenter":{"name":null},"slug":"ashley-williams-if-you-wish-to-learn-es62015-from-scratch-you-must-first-invent-the-universe","source":{"id":"DN4yLZB1vUQ","provider":"youtube"},"tag":[],"title":"Ashley Williams: If you wish to learn ES6/2015 from scratch, you must first invent the universe"},"allTalkJson":null},"pathContext":{"slug":"ashley-williams-if-you-wish-to-learn-es62015-from-scratch-you-must-first-invent-the-universe","tags":[]}}

